# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: #333;
  /* Fallback for when there is no custom background color defined. */
}

blockquote, q {
  quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul, ol {
  margin: 0 0 1.5em 3em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

table {
  margin: 0 0 1.5em;
  width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: #ccc #ccc #bbb;
  border-radius: 3px;
  background: #e6e6e6;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 15px 17px rgba(255, 255, 255, 0.5), inset 0 -5px 12px rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.8);
  font-size: 12px;
  font-size: 0.75rem;
  line-height: 1;
  padding: .6em 1em .4em;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.8);
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
  border-color: #ccc #bbb #aaa;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.8), inset 0 15px 17px rgba(255, 255, 255, 0.8), inset 0 -5px 12px rgba(0, 0, 0, 0.02);
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
  border-color: #aaa #bbb #bbb;
  box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

textarea {
  width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  color: royalblue;
}

a:visited {
  color: purple;
}

a:hover, a:focus, a:active {
  color: midnightblue;
}

a:focus {
  outline: thin dotted;
}

a:hover, a:active {
  outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
  clear: both;
  display: block;
  position: relative;
  background: #fff;
  width: 100%;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
}

.main-navigation:before, .main-navigation:after {
  content: " ";
  display: table;
}

.main-navigation:after {
  clear: both;
}

@media (min-width: 992px) {
  .main-navigation {
    font-family: "Oswald", sans-serif;
  }
}

.main-navigation ul {
  display: none;
  list-style: none;
  margin: 61px 0 0;
  padding-left: 0;
  position: absolute;
  z-index: 999;
  width: 100%;
  background: white;
}

@media (min-width: 992px) {
  .main-navigation ul {
    margin: 0 auto;
    width: auto;
    position: relative;
    flex-flow: row;
    justify-content: center;
  }
}

.main-navigation ul ul {
  flex-flow: column;
  position: relative;
  top: 0;
  left: 0;
  z-index: 99999;
  margin-top: 0;
}

@media (min-width: 992px) {
  .main-navigation ul ul {
    position: absolute;
    left: -999em;
    top: 55px;
  }
}

@media (min-width: 1200px) {
  .main-navigation ul ul {
    top: 59px;
  }
}

.main-navigation ul ul ul {
  position: relative;
  left: 0;
  top: 0;
}

@media (min-width: 992px) {
  .main-navigation ul ul ul {
    position: absolute;
    left: -999em;
  }
}

.main-navigation ul ul ul a {
  padding-left: 90px;
}

@media (min-width: 992px) {
  .main-navigation ul ul ul a {
    padding: 12px 30px;
  }
}

@media (min-width: 992px) {
  .main-navigation ul ul li:hover > ul,
  .main-navigation ul ul li.focus > ul {
    left: 100%;
  }
}

.main-navigation ul ul a {
  line-height: 1.4;
  padding-left: 60px;
}

@media (min-width: 992px) {
  .main-navigation ul ul a {
    width: 200px;
    padding: 12px 30px;
  }
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
  left: auto;
}

.main-navigation li {
  position: relative;
  border-top: 1px solid #efefef;
}

@media (min-width: 992px) {
  .main-navigation li {
    float: left;
    border-bottom: none;
  }
}

.main-navigation a {
  display: block;
  text-decoration: none;
  padding: 15px 35px;
  color: #252525;
  line-height: 35px;
  text-transform: uppercase;
  transition: all 0.3s;
}

.main-navigation a:hover {
  color: #5f5f5f;
}

@media (min-width: 992px) {
  .main-navigation a {
    padding: 10px 15px;
  }
}

@media (min-width: 1200px) {
  .main-navigation a {
    padding: 12px 30px;
  }
}

.main-navigation .menu-item:hover,
.main-navigation .menu-item:hover .sub-menu,
.main-navigation .current_page_ancestor:hover {
  background: #fcfcfc;
}

.main-navigation .menu-item .menu-item:hover,
.main-navigation .menu-item .menu-item:hover .sub-menu,
.main-navigation .menu-item .current_page_ancestor:hover {
  background: #f5f5f5;
}

.main-navigation .menu-item .menu-item .menu-item:hover,
.main-navigation .menu-item .menu-item .menu-item:hover .sub-menu,
.main-navigation .menu-item .menu-item .current_page_ancestor:hover {
  background: #f2f2f2;
}

/* Small menu. */
.menu-toggle,
.menu-toggle:focus
.menu-toggle:active {
  border: none;
  outline: none;
  background: none;
  box-shadow: none !important;
  display: block;
  position: relative;
  width: 40px;
  height: 31px;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  margin: 15px;
  float: right;
  overflow: hidden;
}

.menu-toggle span {
  display: block;
  position: absolute;
  height: 6px;
  width: 50%;
  background: #252525;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.menu-toggle span:nth-child(even) {
  left: 50%;
  border-radius: 0 9px 9px 0;
}

.menu-toggle span:nth-child(odd) {
  left: 0px;
  border-radius: 9px 0 0 9px;
}

.menu-toggle span:nth-child(1), .menu-toggle span:nth-child(2) {
  top: 0px;
}

.menu-toggle span:nth-child(3), .menu-toggle span:nth-child(4) {
  top: 12px;
}

.menu-toggle span:nth-child(5), .menu-toggle span:nth-child(6) {
  top: 24px;
}

.menu-toggle.open span:nth-child(1), .menu-toggle.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu-toggle.open span:nth-child(2), .menu-toggle.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.menu-toggle.open span:nth-child(1) {
  left: 5px;
  top: 7px;
}

.menu-toggle.open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 7px;
}

.menu-toggle.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
}

.menu-toggle.open span:nth-child(4) {
  left: 100%;
  opacity: 0;
}

.menu-toggle.open span:nth-child(5) {
  left: 5px;
  top: 17px;
}

.menu-toggle.open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 17px;
}

.menu-toggle,
.main-navigation.toggled ul {
  display: block;
}

@media (min-width: 992px) {
  .menu-toggle,
  .main-navigation.toggled ul {
    display: flex;
  }
}

@media (min-width: 992px) {
  .menu-toggle {
    display: none;
  }
  .main-navigation ul {
    display: flex;
  }
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before:before, .clear:before:after,
.clear:after:before,
.clear:after:after,
.entry-content:before:before,
.entry-content:before:after,
.entry-content:after:before,
.entry-content:after:after,
.comment-content:before:before,
.comment-content:before:after,
.comment-content:after:before,
.comment-content:after:after,
.site-header:before:before,
.site-header:before:after,
.site-header:after:before,
.site-header:after:after,
.site-content:before:before,
.site-content:before:after,
.site-content:after:before,
.site-content:after:after,
.site-footer:before:before,
.site-footer:before:after,
.site-footer:after:before,
.site-footer:after:after {
  content: " ";
  display: table;
}

.clear:before:after,
.clear:after:after,
.entry-content:before:after,
.entry-content:after:after,
.comment-content:before:after,
.comment-content:after:after,
.site-header:before:after,
.site-header:after:after,
.site-content:before:after,
.site-content:after:after,
.site-footer:before:after,
.site-footer:after:after {
  clear: both;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5em;
  /* Make sure select elements fit in widgets. */
}

.widget select {
  max-width: 100%;
}

.images-grid figure {
  margin: 0;
  overflow: hidden;
  width: 25%;
  max-height: 290px;
  float: left;
}

.images-grid figure img {
  display: block;
  transition: all 0.5s ease-in-out;
}

.images-grid figure img:hover {
  transform: scale(1.1);
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-branding {
  background-size: cover;
  background-position: center;
  background-color: #efefef;
  height: 45vh;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: 30px;
  border-bottom: 19px solid #30a702;
}

.site-branding:before, .site-branding:after {
  content: " ";
  display: table;
}

.site-branding:after {
  clear: both;
}

.site-branding .site-logo,
.site-branding .site-title,
.site-branding .site-description {
  text-align: center;
  display: block;
  color: #333;
}

.site-branding img {
  display: block;
}

/*--------------------------------------------------------------
## Page
--------------------------------------------------------------*/
#page {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  min-height: 100%;
}

#page h1 {
  text-align: center;
  font-size: 280%;
  margin: 0.5em 0 1.5em;
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
}

#page h1::after {
  content: '';
  display: block;
  width: 180px;
  height: 6px;
  margin: 20px auto;
  background: #30a702;
}

#page .intro {
  font-size: 135%;
  line-height: 2;
}

#page hr {
  margin: 70px 0 20px;
}

/*--------------------------------------------------------------
## Content
--------------------------------------------------------------*/
.site-content {
  background: #fff;
  padding: 60px 30px;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
footer {
  background: #333;
  padding: 60px 30px;
  text-align: center;
  color: #fff;
}

footer a,
footer a:hover,
footer a:active,
footer a:visited {
  color: #fff;
  text-decoration: none;
}

.menu-social {
  margin-bottom: 30px;
}

.menu-social ul {
  padding: 0;
  margin: 0;
}

.menu-social li {
  display: inline-block;
}

.menu-social li a {
  color: #333;
  text-decoration: none;
  background: #fff;
  padding: 10px;
  display: block;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  line-height: 1.1;
  font-size: 20px;
  margin: 0 10px;
}

.menu-social li a:before {
  font-family: FontAwesome;
}

.menu-social li a[href*="facebook.com"]::before {
  content: '\f09a';
}

.menu-social li a[href*="twitter.com"]::before {
  content: '\f099';
}

.menu-social li a[href*="dribbble.com"]::before {
  content: '\f17d';
}

.menu-social li a[href*="plus.google.com"]::before {
  content: '\f0d5';
}

.menu-social li a[href*="pinterest.com"]::before {
  content: '\f0d2';
}

.menu-social li a[href*="github.com"]::before {
  content: '\f09b';
}

.menu-social li a[href*="tumblr.com"]::before {
  content: '\f173';
}

.menu-social li a[href*="youtube.com"]::before {
  content: '\f167';
}

.menu-social li a[href*="flickr.com"]::before {
  content: '\f16e';
}

.menu-social li a[href*="vimeo.com"]::before {
  content: '\f194';
}

.menu-social li a[href*="instagram.com"]::before {
  content: '\f16d';
}

.menu-social li a[href*="linkedin.com"]::before {
  content: '\f0e1';
}

/*--------------------------------------------------------------
## Content Sidebar
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: block;
}

.hentry {
  margin: 0 0 1.5em;
}

.byline,
.updated:not(.published) {
  display: none;
}

.single .byline,
.group-blog .byline {
  display: inline;
}

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption .wp-caption-text {
  margin: 0.8075em 0;
}

.wp-caption-text {
  text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}
